      <span class="hljs-keyword">this</span>.$refs.scrollerEvent.reset({<span class="hljs-attr">top</span>: <span class="hljs-number">0</span>})
    })
    <span class="hljs-keyword">this</span>.$nextTick(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
      <span class="hljs-keyword">this</span>.$refs.scrollerBottom.reset({<span class="hljs-attr">top</span>: <span class="hljs-number">0</span>})
    })
  },
  <span class="hljs-attr">methods</span>: {
    onScrollBottom () {
      <span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.onFetching) {
