      } <span class="hljs-keyword">else</span> {
        <span class="hljs-keyword">this</span>.onFetching = <span class="hljs-literal">true</span>
        setTimeout(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-keyword">this</span>.bottomCount += <span class="hljs-number">10</span>
          <span class="hljs-keyword">this</span>.$nextTick(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
            <span class="hljs-keyword">this</span>.$refs.scrollerBottom.reset()
          })
          <span class="hljs-keyword">this</span>.onFetching = <span class="hljs-literal">false</span>
        }, <span class="hljs-number">2000</span>)
      }
    },
